




































































import Vue from "vue";

export default Vue.extend({
  name: "AipParameters",

  data: () => ({
    items: [],
    open: [],
    search: null,
    caseSensitive: false,
  }),

  computed: {
    filter() {
      return this.caseSensitive
        ? (item, search, textKey) => item[textKey].indexOf(search) > -1
        : undefined;
    },
  },
});
